.wrapper{
    position:fixed;
    width:100%;
    height:100%;
    top:0px;
    left:0px;
    z-index:1000;
    display: flex;
    flex-direction: column;
    margin: 10px;
    /* justify-content: space-evenly; */
    align-items: center;
}

.row {
    height: 100%;
    width: 100%;
    display: flex;
    align-items:flex-start;
    gap: 5%;
}

@media only screen and (max-width: 768px) {
    .row { 
        flex-wrap: wrap;
        gap: 5%;
        height: auto;
    }
    .talabel {
        margin-bottom: 5%;
    }
    #arrow {
        display: none;
    }
}

button[value=true] {
    display: none;
}


#translatebutton {
    width: 80px;
}

.LanguageSelect {
    /* width: 40%; */
    display:flex;
    align-items: center;
    gap: 11%;
}

form {
    width: 90%;
    height: 70%;
}


hr {
    margin-bottom: 25px;
}

.header {
    width: 98%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    margin-top: 5px;
    margin-right: 1%;
}

.flags {
    display: flex;
    flex-direction: row;
    gap: 5px;
    width:auto;
    align-self: center;
}

.talabel{
display: flex;
flex-direction: column;
width: 100%;
height: 100%;
gap: 10px;
}

textarea[name="postContent"] {
    width: 100%;
    height: 30%;
}

.title {
    display: flex;
    flex-direction: row;
    gap: 3%;
    width: 50%;
}